*{
  margin: 0;
  padding :0;
  box-sizing: border-box;
  background-color: #d2dddb;
}

.heading_style{
  padding : 30px 0;
  text-align: center;
  font-family: "Releway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.container{
  width: 100%;
  height: auto;
  
}

.card{
  margin : 5%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1); 
  background-color: blanchedalmond;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0,0,0,0.1);
  float: left;
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0,0,0,0.1);
  transform: scale(1.05, 1.05);
}

.card_img{
  width: 100%;
  height: 220px;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 2px;
}

.cardinfo{
  background-color: #fff;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
}

.card_category{
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
}

.card_title{
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  background-color: #fff;
  text-transform: capitalize;
}

button {
  padding: 5px 8px;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border : 1px solid black;
  cursor: pointer;
}

@media screen and (max-width:600px) {
  .cardinfo{
    background-color: #fff;
    border-bottom-right-radius: 12px;
    border-bottom-left-radius: 12px;
    padding: 16px 24px 24px 24px;
    background-color: #fff;
  }
  
  .card_category{
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-size: 10px;
    letter-spacing: 1px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
    margin-left: 4px;
    text-align: center;
    float: right;
    padding-bottom: 5px;
  }
  
  .card_title{
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: "Raleway", sans-serif;
    background-color: #fff;
    text-transform: capitalize;
    margin-left: -20px;
    font-size: 10px;
  }
  
  button {
    padding: 5px 8px;
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-size: 10px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
    outline: none;
    border : 1px solid black;
    cursor: pointer;
    margin-left: -20px;
  }
}